import React, { PropTypes } from 'react';
import { Link } from 'react-router';

import '../App.css';
import ClothesSelector from './ClothesSelector';

import { Wrapper, Block, Header2, Title2, Button3, Center } from './styledComponents';

const Step1 = ({ orderItems, addItem }) => (
  <Wrapper>
    <Block>
      <Header2>
        <Title2>
          Je choisis mes vêtements
        </Title2>
      </Header2>
      <ClothesSelector
        orderItems={orderItems}
        addItem={slug => addItem(slug)}
      />
      <Center>
        <Link to={'/order/step2'}><Button3>Valider ces vêtements</Button3></Link>
      </Center>
    </Block>
  </Wrapper>
);

Step1.propTypes = {
  orderItems: PropTypes.arrayOf(PropTypes.shape()).isRequired,
  addItem: PropTypes.func.isRequired,
};

export default Step1;



